body,
input,
textarea,
select,
button {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
}
body {
  background-color: #322e2d;
  color: white;
  background-image: url('assets/bg_antracit.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

input {
  padding: 0.5rem;
  border: 1px solid #95989a;
  border-radius: 0.25rem;
}
@import url('../node_modules/material-design-icons/iconfont/material-icons.css');
@import url('../node_modules/react-grid-layout/css/styles.css');
@import url('../node_modules/@speedlo/assets/flaticon.css');

/* react-grid disabling animation */
.react-grid-item {
  transition: all 0s;
}

/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
